.header-logos {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 87vh;
  padding: 0 30px; }
  .header-logos-item {
    width: 90px;
    height: 80px;
    display: flex;
    -webkit-transition: 0.5s ease;
    transition: 0.5s ease; }
    .header-logos-item.lakers {
      margin-top: 10px;
      background: url(/NBA/static/media/lakers.e125d45d.png) no-repeat center;
      background-size: contain; }
    .header-logos-item.houston {
      background: url(/NBA/static/media/rockets.070190ce.png) no-repeat center;
      background-size: contain; }
    .header-logos-item.warriors {
      background: url(/NBA/static/media/warriors.c6f96cce.png) no-repeat center;
      background-size: contain; }
    .header-logos-item.toronto {
      background: url(/NBA/static/media/raptrors.262d3164.png) no-repeat center;
      background-size: contain; }
    .header-logos-item.bucks {
      background: url(/NBA/static/media/bucks.65c9fe3f.png) no-repeat center;
      background-size: contain; }
    .header-logos-item.celtics {
      background: url(/NBA/static/media/celtics.4e7b60a0.png) no-repeat center;
      background-size: contain; }
    .header-logos-item:hover {
      height: 90px;
      width: 90px; }
  .header-logos-link {
    font-size: 0;
    width: 100%; }

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
	overflow: hidden;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
.fade-enter {
  opacity: 0;
  z-index: 1;
}
.fade-enter.fade-enter-active {
  opacity: 1;
  -webkit-transition: opacity 5000ms ease-in;
  transition: opacity 5000ms ease-in;
}


